import { useBudget } from '../context/BudgetContext';
import { Doughnut } from 'react-chartjs-2';
import { Chart as ChartJS, ArcElement, CategoryScale, Title, Tooltip, Legend } from 'chart.js';

ChartJS.register(ArcElement, CategoryScale, Title, Tooltip, Legend);

const Graph: React.FC = () => {
  const { expenses, income } = useBudget();

  const getCategoryTotals = (data: { category: string; amount: number }[]) => {
    return data.reduce((acc: { [key: string]: number }, curr) => {
      acc[curr.category] = acc[curr.category] ? acc[curr.category] + curr.amount : curr.amount;
      return acc;
    }, {});
  };

  const expenseTotals = getCategoryTotals(expenses);
  const incomeTotals = getCategoryTotals(income);

  const data = {
    labels: Object.keys(expenseTotals),
    datasets: [
      {
        label: 'Expenses',
        data: Object.values(expenseTotals),
        backgroundColor: ['red', 'blue', 'green'],
      },
      {
        label: 'Income',
        data: Object.values(incomeTotals),
        backgroundColor: ['yellow', 'orange', 'purple'],
      },
    ],
  };

  return (
    <div>
      <h2>Income vs Expenses</h2>
      <Doughnut data={data} />
    </div>
  );
};

export default Graph;