import { useBudget } from '../context/BudgetContext';

const ExpenseCategory: React.FC = () => {
  const { expenses, budgetLimits } = useBudget();

  const totalExpense = (category: string) => {
    return expenses.filter(exp => exp.category === category)
      .reduce((sum, exp) => sum + exp.amount, 0);
  };

  return (
    <div>
      <h2>Expense Categories</h2>
      {Object.keys(budgetLimits).map((category) => {
        const categoryTotal = totalExpense(category);
        const limit = budgetLimits[category];
        const warning = categoryTotal >= limit * 0.8;

        return (
          <div key={category}>
            <h3>{category}</h3>
            <p>Total Expense: {categoryTotal} / Limit: {limit}</p>
            {warning && <p style={{ color: 'red' }}>Warning: 80% of your budget limit reached!</p>}
          </div>
        );
      })}
    </div>
  );
};

export default ExpenseCategory;